/** * Design Tokens Index * Central export point for all design token systems */// Core unified tokensexport {   coreTokens,   computedStyles,   designUtils,   petersenTheme,  type CoreTokens,  type ComputedStyles,  type DesignUtils,  type PetersenTheme} from '../components/extracted/coreDesignTokens';// Enhanced gaming tokensexport {   enhancedGamingTokens,  type EnhancedGamingTokens } from './enhancedGamingTokens';// Quantum-spatial tokensexport {   quantumSpatialTokens,  type QuantumSpatialTokens } from './quantumSpatialTokens';// Apple HIG tokensexport {   appleHIGTokens,  liquidGlassTokens,  type AppleHIGTokens,  type LiquidGlassTokens } from './appleHIGTokens';// Unified design tokensexport {  unifiedTokens,  unifiedDesignTokens,  type UnifiedTokens,  type UnifiedDesignTokens} from './unifiedTokens';// Re-export existing token files if they exist and are properly structured// export * from './enhancedDesignTokens';// export * from './liquidGlassTokens';// export * from './quantumTokens';// Unified token merger utilityexport const mergeTokens = <T extends Record<string, any>>(...tokenSets: T[]): T => {  return tokenSets.reduce((merged, tokens) => ({    ...merged,    ...tokens,    // Deep merge for nested objects    colors: { ...merged.colors, ...tokens.colors },    spacing: { ...merged.spacing, ...tokens.spacing },    typography: { ...merged.typography, ...tokens.typography },    gradients: { ...merged.gradients, ...tokens.gradients },    shadows: { ...merged.shadows, ...tokens.shadows },    animation: { ...merged.animation, ...tokens.animation },  }), {} as T);};// Token selection utilityexport const selectTokenSystem = (theme: 'core' | 'gaming' | 'quantum' | 'apple' | 'petersen') => {  switch (theme) {    case 'core':      return coreTokens;    case 'gaming':      return enhancedGamingTokens;    case 'quantum':      return quantumSpatialTokens;    case 'apple':      return appleHIGTokens;    case 'petersen':      return petersenTheme;    default:      return coreTokens;  }};